<template>
  <div class="list row">
    <div class="col-md-6">
      <h4>Products List</h4>
      <ul class="list-group">
        <li
          class="list-group-item"
          :class="{ active: index == currentIndex }"
          v-for="(product, index) in products"
          :key="index"
          @click="setActiveProduct(product, index)"
        >
          {{ product.name }}
        </li>
      </ul>
    </div>
    <div class="col-md-6" style="margin-bottom: 32px">
      <div v-if="currentProduct._id">
        <h4>Product</h4>
        <div>
          <label><strong>ID:</strong></label> {{ currentProduct._id }}
        </div>
        <div>
          <label><strong>Name:</strong></label> {{ currentProduct.name }}
        </div>
        <div>
          <label><strong>Category:</strong></label>
          {{ currentProduct.category }}
        </div>
        <div>
          <label><strong>Detail:</strong></label>
          {{ currentProduct.detail }}
        </div>
        <div>
          <label><strong>Material:</strong></label>
          {{ currentProduct.material }}
        </div>
        <div>
          <label><strong>Size:</strong></label>
          {{ currentProduct.size }}
        </div>
        <div>
          <label><strong>ArImages:</strong></label>
          <div style="display: flex">
            <img class="display-image" :src="currentProduct.ArImages[0].url" />
          </div>
        </div>
        <div>
          <label><strong>Inner radius:</strong></label>
          {{ currentProduct.ArImages[0].inner_radius }}
        </div>
        <div>
          <label><strong>Images:</strong></label>
          <div style="display: flex">
            <div v-for="(key, image) in currentProduct.images" :key="key">
              <img
                class="display-image"
                :src="currentProduct.images[image].url"
              />
            </div>
          </div>
        </div>

        <router-link
          :to="'/products/' + currentProduct._id"
          class="badge badge-warning"
          >Edit</router-link
        >
      </div>
      <div v-else>
        <br />
        <p>Please click on a Product...</p>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent } from "vue";
import DataService from "@/services/DataService";
import Product from "@/types/Product";
import ResponseData from "@/types/ResponseData";

export default defineComponent({
  name: "products-list",
  data() {
    return {
      products: [] as Product[],
      currentProduct: {} as Product,
      currentIndex: -1,
      name: "",
    };
  },
  methods: {
    retrieveProducts() {
      DataService.getAll(null)
        .then((response: ResponseData) => {
          this.products = response.data.product;
        })
        .catch((e: Error) => {
          console.log(e);
        });
    },

    refreshList() {
      this.retrieveProducts();
      this.currentProduct = {} as Product;
      this.currentIndex = -1;
    },

    setActiveProduct(product: Product, index = -1) {
      this.currentProduct = product;
      this.currentIndex = index;
    },
  },
  mounted() {
    this.retrieveProducts();
  },
});
</script>

<style>
.list {
  text-align: left;
  max-width: 750px;
  margin: auto;
}
.display-image {
  height: 100px;
  object-fit: contain;
  margin: 4px;
}
.badge-warning {
  margin-top: 12px;
  border: none;
  border-radius: 6px;
  padding: 16px 24px;
}
</style>
